body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

.square {
  background: #fff;
  border: 1px solid #000;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 60px;
  height: 60px;

  display: flex; /* Use flexbox to center the content */
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
}

.image-square {
  width: 100%;
  height: 100%;
  padding: 0;
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Specific style for images inside the buttons */
.image-square img {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: auto;
  object-fit: contain; /* This will make sure the image is scaled properly within the button */
}


.image-square:hover {
  background-color: #45a049; /* Darker green on hover */
}

/* General styles for the app container */
.app {
  text-align: center;
  font-family: 'Arial', sans-serif;
  margin-top: 50px;
  display: flex; /* Enable flexbox */
  flex-direction: column; /* Stack children vertically */
  align-items: center; /* Center children horizontally */
  justify-content: center; /* Center children vertically */
  min-height: 50vh; /* Full viewport height */
}

/* Styles for the heading */
.app h1 {
  color: #333;
  font-size: 2em;
  margin-bottom: 20px;
}

/* General styles for buttons */
.menuButton {
  background-color: #4CAF50; /* Green background */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  
  width: 200px;
  height: 50px;
  overflow: hidden; /* Prevents text from overflowing */
  white-space: nowrap; /* Prevents text wrapping */
  text-overflow: ellipsis;
  
  border-radius: 10px; /* Rounded edges */
}

.menuButton:hover {
  background-color: #45a049; /* Darker green on hover */
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.row {
  display: flex;
}
.row:nth-child(odd) .square:nth-child(odd),
.row:nth-child(even) .square:nth-child(even) {
  background-color: #f0d9b5; /* light color square */
}

.row:nth-child(odd) .square:nth-child(even),
.row:nth-child(even) .square:nth-child(odd) {
  background-color: #b58863; /* dark color square */
}

.status {
  margin-bottom: 10px;
}
.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
